    <nav>
      <Link to="/">Home</Link>
      <Link to="/section1">Lab실</Link>
      <Link to="/section2">전임 교수</Link>
      <Link to="/section3">연구원</Link>
      <Link to="/section4">논문</Link>
      <Link to="/section5">과제</Link>
    </nav>
