import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';  // 기본 스타일
import App from './App';  // 최상위 App 컴포넌트
import reportWebVitals from './reportWebVitals';

// ReactDOM을 사용하여 App 컴포넌트를 HTML의 root 요소에 렌더링
ReactDOM.render(
  <React.StrictMode>
    <App />
  </React.StrictMode>,
  document.getElementById('root')
);

// 성능 측정을 위해 웹 비탈리티 리포트 사용 (선택 사항)
reportWebVitals();
